/* eslint-disable no-param-reassign */
import React, { useState } from 'react';
import { Button, FormGroup, Row } from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';

import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import AppError from '../../../../helpers/AppError';

import IntlMessages from '../../../../helpers/IntlMessages';
import { getHackathonInfo } from '../../../../helpers/Utils';
import { adminRoot } from '../../../../constants/defaultValues';

// import TopNavigation from '../../../../components/wizard/TopNavigation';
// import BottomNavigation from '../../../../components/wizard/BottomNavigation';

const SelectQuestions = React.lazy(() =>
  import(
    /* webpackChunkName: "application-step-one" */ './components/SelectQuestions'
  )
);
const AddCustomQuestion = React.lazy(() =>
  import(
    /* webpackChunkName: "application-step-one" */ './components/AddCustomQuestion'
  )
);
const SubmitButton = React.lazy(() =>
  import(
    /* webpackChunkName: "application-step-one" */ './components/SubmitButton'
  )
);

const Application = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');

  const {
    application = {
      about: [],
      education: [],
      experience: [],
      online_profiles: [],
      contact: [],
    },
  } = getHackathonInfo(id);

  const [formValues, setFormValues] = useState({
    id,
    application,
  });

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.application" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <SelectQuestions
            formValues={formValues}
            setFormValues={setFormValues}
          />
          <AddCustomQuestion
            id={id}
            formValues={formValues}
            setFormValues={setFormValues}
          />
          <SubmitButton formValues={formValues} setFormValues={setFormValues} />
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/basics/${id}`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/links/${id}`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Application;
